import request from '@/utils/request';
var api = {
  GetByDeviceId: '/api/dms/DeviceLocation/GetByDeviceId',
  // GetAll: '/api/dms/DeviceLocation/GetAll',
  create: '/api/dms/DeviceLocation/create',
  UpdateInfo: '/api/dms/DeviceLocation/UpdateInfo',
  Delete: '/api/dms/DeviceLocation/delete' // GetList: '/api/dms/DeviceLocation/GetList'

}; // 根据设备Id、keyword查询设备的全部部位

export function GetByDeviceId(obj) {
  return request({
    url: api.GetByDeviceId,
    method: 'get',
    params: obj
  });
} // // 获取全部设备部位信息
// export function GetAll() {
//   return request({
//     url: api.GetAll,
//     method: 'get'
//   })
// }
// 新增设备部位信息

export function create(obj) {
  return request({
    url: api.create,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 修改设备部位信息  

export function UpdateInfo(obj) {
  return request({
    url: api.UpdateInfo,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 删除设备部位信息

export function Delete(id) {
  return request({
    url: api.Delete,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: id
  });
} // // 根据关键字模糊查询
